.card {
  width: 300px;
  height: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  opacity: 0.9;
  margin: 50px;
}

a {
  color: inherit;
  text-decoration: none;
}

.borderLine {
  border-right: 1px solid grey;
  height: 55px;
}

.card ul {
  margin: 0;
  margin-bottom: 12px;
  list-style-type: none;
  padding: 0;
}

.horizontalAlign {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media (max-width: 700px) {
  .card {
    width: auto;
    height: auto;
    max-height: 30%;
    max-width: 90%;
  }
}



